//  加入购物车动画组件
import shopCarAnimation from '@/components/fly-in-cart/fly-in-cart.vue'
import {mapState} from "vuex"
export default {
	computed:{
		...mapState([
			'userMsgs'
		])
	},
	data() {
		return {
			recomendList: [],  //课程列表
		};
	},
	onLoad(options) {
		// console.log("options", options);
		this.$http.lookDetailClass({
			class:options.class
		}).then(res => {
			// console.log("class", res);
			this.recomendList = res.data
		})
	},
	components: {
		shopCarAnimation
	},
	methods: {
		// 跳转到详情页
		toDetail(id) {
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsid='+id
			});
		},
		// 加入购物车
		addShopCar(e) {
			// console.log('加入购物车id=', e.currentTarget.id);
			// console.log("userMsgs", this.userMsgs);
			this.$http.insertIntoCart({
				bookid:e.currentTarget.id,
				userid:this.userMsgs.id
			}).then(res => {
				if(res.data == 0) {
					uni.showToast({
						title: '商品加入购物车失败',
						icon:'none',
						duration:3000
					});
				}else if (res.data == 1) {
					uni.showToast({
						title: '成功加入购物车',
						icon:'none',
						duration:3000
					});
				}
			})
			// 成功的话，调用加入购物车动画
			this.$refs.carAnmation.touchOnGoods(e);
		}
	}
};
